@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;500;600;700&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

.ant-layout-sider {
  z-index: 1;
}

.ant-layout-content {
  position: relative;
}

#components-layout-demo-responsive .logo {
  height: 32px;
  margin: 16px;
  background: rgba(255, 255, 255, 0.2);
}

.site-layout-sub-header-background {
  background: #fff;
}

.site-layout-background {
  background: #fff;
}

.layout-container {
  width: 100vw;
  height: 100vh;
}

.resume-title,
.portfolio-title,
.contact-title {
  text-align: center;
  padding-top: 24px;

  h2 {
    font-size: 50px;
    font-weight: 800;
    margin-bottom: 0;
  }
}

.down-link {
  text-decoration: none;
  color: white;
  padding-left: 10px;
}